import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => {
  const { left, borderLeft, marginLeft, paddingLeft } = theme.mixins;

  return {
    heading: {
      width: pxToRem(250),
      [theme.breakpoints.up('lg')]: {
        // position: 'absolute',
        width: pxToRem(500),
        // top: theme.spacing(5),
      },
    },
    exploreButton: {
      marginTop: pxToRem(16),
    },
    wrapper: {
      position: 'relative',
      flexDirection: 'column',
      ...theme.shape.componentSpacing,
      '& .MuiTabs-root': {
        marginTop: theme.spacing(4),
        marginBottom: theme.spacing(4),
      },

      '& .collage-tabs': {
        marginTop: pxToRem(49),
        marginBottom: pxToRem(32),
      },

      '& .tabs-container': {
        width: '100%',
        backgroundColor: 'transparent',
        '& .tabs-desktop-view': {
          [theme.breakpoints.down('md')]: {
            display: 'none',
          },
        },

        [theme.breakpoints.up('lg')]: {
          // marginTop: theme.isDirectionRTL ? pxToRem(216) : pxToRem(192),

          '& .tabPanelContainer': {
            display: 'flex',
          },
        },
      },
    },
    root: {
      [theme.breakpoints.down('lg')]: {
        width: '100%',
      },
      [theme.breakpoints.down('md')]: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
      },
    },
    cardWrapper: {
      border: 0,
      borderRadius: 0,
      background: 'transparent',
      [theme.breakpoints.down('md')]: {
        height: '100%',
        borderBottom: `1px solid ${theme.mixins.Black(200)}`,
      },

      '& .card-content': {
        [theme.breakpoints.down('md')]: {
          padding: `${theme.spacing(2)} ${theme.spacing(2)}`,
          ...paddingLeft(0),
          borderBottom: 0,
        },
        [theme.breakpoints.up('lg')]: {
          padding: `${theme.spacing(4)} ${theme.spacing(2)}`,
        },
        '& p': {
          display: 'flex',
          color: theme.palette.text.tertiary,
        },
      },
      '& .card-title': {
        marginBottom: theme.spacing(1),
        [theme.breakpoints.down('md')]: {
          overflow: 'hidden',
        },
      },
    },

    'card-0': {
      width: '100%',
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.paddingRight(theme.spacing(2)),
      },
    },
    row1: {
      ...theme.mixins.paddingRight(theme.spacing(3)),
    },
    row2: {
      position: 'relative',
      top: 0,
      ...left('59.5%'),
      height: '100%',
      marginTop: '-15%',

      [theme.breakpoints.up('lg')]: {
        top: 'inherit',
        ...left('inherit'),
      },
      [theme.breakpoints.up('xxxl')]: {
        marginTop: '-12%',
      },
    },

    'card-1': {
      position: 'relative',
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.left('-25%'),
        width: 'calc(100% + 25%)',
      },
      '& .card-title': {
        marginBottom: theme.spacing(1),
      },
      '& .card-media': {
        [theme.breakpoints.up('lg')]: {
          ...borderLeft(theme.borders.collageBorder),
          borderBottom: theme.borders.collageBorder,
        },
      },
      '& .card-content': {
        marginBottom: 0,

        [theme.breakpoints.up('lg')]: {
          ...marginLeft('20.5%'),
          marginTop: `-${theme.spacing(5)}`,
          marginBottom: theme.spacing(5),
        },
      },
    },
    'card-2': {
      '& .card-content': {
        [theme.breakpoints.up('lg')]: {
          padding: `${theme.spacing(4)} ${theme.spacing(2)}`,
          paddingBottom: `${theme.spacing(4)}`,
        },
      },
    },
    gridList: {
      flexWrap: 'nowrap',
      // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
      transform: 'translateZ(0)',

      '& .item': {
        flex: '0 0 auto',
        paddingBottom: '0 !important',

        '& .card-link-wrapper': {
          width: '100%',
        },

        [theme.breakpoints.up('lg')]: {
          marginTop: pxToRem(20),
        },
      },
    },
  };
});

export default useStyles;
