import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import CardMedia from '@material-ui/core/CardMedia';

import Picture from '../Picture';
import useStyles from './style';

const CardMediaMUI = ({
  className,
  image,
  mobileImage,
  scaleOnHover,
  aspectRatio,
  alternateAltText,
}) => {
  const classes = useStyles(className);
  const imgHoverClass = scaleOnHover ? 'img-hover' : '';

  return (
    <CardMedia className={clsx(classes.media, imgHoverClass, className)}>
      <Picture
        media={{
          image,
          mobileImage,
          aspectRatio,
        }}
        alternateAltText={alternateAltText}
      />
    </CardMedia>
  );
};

CardMediaMUI.propTypes = {
  className: PropTypes.string,
  image: PropTypes.shape({
    src: PropTypes.string,
    alt: PropTypes.string,
  }).isRequired,
  scaleOnHover: PropTypes.bool,
  alternateAltText: PropTypes.string,
  aspectRatio: PropTypes.shape({
    mobile: PropTypes.string,
    desktop: PropTypes.string,
  }),
};

CardMediaMUI.defaultProps = {
  className: '',
  scaleOnHover: true,
  aspectRatio: {},
};

export default CardMediaMUI;
