import React, { useEffect, useRef, createRef, Fragment } from 'react';
import { withNamespaces } from 'react-i18next';
import { withSitecoreContext, Text } from '@sitecore-jss/sitecore-jss-react';
import clsx from 'clsx';
import get from 'lodash/get';
import { Hidden, GridList, Fade } from '@material-ui/core';
import { TESTCOMPONENT_CONTAINER } from './locators';
import Grid from '../../../components/molecules/Grid';
import Typography from '../../../components/atoms/Typography';
import CardMedia from '../../../components/atoms/CardMedia';
import Card from '../../../components/molecules/Card';
import CardContent from '../../../components/atoms/CardContent';
import Tabs from '../../../components/molecules/Tabs';
import Title from '../../../components/atoms/Title';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import PrefetchImages from '../../../utils/PrefetchImages';
import { pushAnaylyticsData } from '../../../../utils/analytics';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';
import Button from '../../../components/atoms/Button';
import useStyles from './style';
import {
  getPageName,
  getComponentName,
} from '../../../utils/getPageComponentInfo';
import { createDynamicLabels } from '../../../../utils/analytics';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import TextRotateAnimation from '../../../components/atoms/TextRotateAnimation';

const CollageComponent = props => {
  const { fields } = props;

  const classes = useStyles();
  if (!fields) {
    return null;
  }

  const { title, segmentText, collageTabs, cta } = fields;

  const addTileAnalyticsInfo = imgTitle => {
    const obj = {
      ...get(analyticsData, 'pageBodyInteraction.tileClick'),
      ...{
        label: createDynamicLabels([
          getPageName(props),
          getComponentName(props),
          `${title && title.value}-${imgTitle && imgTitle.value}`,
        ]),
      },
    };
    pushAnaylyticsData(obj);
  };
  const onTabChange = index => {
    const obj = {
      ...get(analyticsData, 'pageBodyInteraction.miniNavClick'),
      ...{
        label: createDynamicLabels([
          getPageName(props),
          getComponentName(props),
          `${title && title.value}-${get(
            collageTabs[index],
            'tabText.value',
            ''
          )}`,
        ]),
      },
    };
    pushAnaylyticsData(obj);
  };
  const renderCard = (tile, index = 0) => {
    const image =
      (get(tile, 'image.value.src', '') && tile.image) ||
      get(tile, 'gallery[0].fields.image', '');
    const mobileImage =
      (get(tile, 'mobileImage.value.src', '') && tile.mobileImage) ||
      get(tile, 'gallery[0].fields.mobileImage', '');
    const link =
      (get(tile, 'collageLink.value.href', '') && tile.collageLink) ||
      (tile && { value: { href: tile.link } }); // This is a case where if link object is not present then direct link href value will be provided
    const title = tile && (tile.title || tile.name);
    const location = tile && tile.location;

    const getCardTimeoutDuration = () => {
      setTimeout(() => { }, 300); // This will prevent fading start to all the cards at simultaneously
      if (index === 0) {
        return (index + 1) * 500;
      }
      return index * 700;
    };

    return (
      <Fade in={true} key={index} timeout={getCardTimeoutDuration()}>
        <Card
          className={clsx(
            classes.root,
            classes.cardWrapper,
            classes[`card-${index}`]
          )}
          key={index}
          square
          link={link}
          onClick={() => addTileAnalyticsInfo(title)}
        >
          <CardMedia
            className="card-media"
            image={image}
            mobileImage={mobileImage}
            aspectRatio={{ mobile: '2x1', desktop: '2x1' }}
            alternateAltText={title && title.value}
          />
          <CardContent className={clsx(classes.content, 'card-content')}>
            {title && (
              <Typography
                variant="h5"
                className="card-title"
                component="h3"
                wordBreak
              >
                <Text field={title} />
              </Typography>
            )}
            {location && (
              <Typography variant="body2" color="textSecondary" component="p">
                <Text field={location} />
              </Typography>
            )}
          </CardContent>
        </Card>
      </Fade>
    );
  };

  const preProcessCollageImages = items =>
    items.map(item => {
      const image = get(item, 'fields.image.value[2x1]', '');
      return image
        ? {
          ...item,
          fields: {
            ...item.fields,
            image: {
              ...item.fields.image,
              value: {
                ...item.fields.image.value,
                ['2x1']: {
                  ...item.fields.image.value['2x1'],
                  large: image.medium,
                  large2x: image.medium2x,
                  xLarge: image.large,
                  xLarge2x: image.large2x,
                },
              },
            },
          },
        }
        : item;
    });

  const tabData = collages => {
    if (!collages || collages.length < 1) return null;
    const [firstCollage, ...restCollages] = collages;

    return (
      <Fragment>
        <Grid container classes={{ root: 'tabs-desktop-view' }}>
          <Grid item md={8} className={`${classes.row1} row1`}>
            {renderCard(firstCollage.fields, 2)}
          </Grid>
          <Grid item md={4} className={`${classes.row2} row2`}>
            {restCollages &&
              preProcessCollageImages(restCollages).map((collage, index) =>
                renderCard(collage.fields, index + 1)
              )}
          </Grid>
        </Grid>
        <Hidden lgUp implementation="css">
          <GridList
            className={classes.gridList}
            cols={1.3}
            cellHeight="auto"
            spacing={32}
          >
            {collages &&
              collages.map((collage, index) => (
                <li className="item" key={index}>
                  {renderCard(collage.fields, index)}
                </li>
              ))}
          </GridList>
          {get(cta, 'value.text') && (
            <Button
              hasBorder={false}
              field={cta}
              className={classes.exploreButton}
              size="small"
            />
          )}
        </Hidden>
      </Fragment>
    );
  };

  const createTabs =
    collageTabs &&
    collageTabs.map(collageTab => {
      const { collages, tabText } = collageTab || '';

      return {
        data: tabData(collages),
        label: tabText && tabText.value,
      };
    });

  let prefetchImagesDesktop = [];
  let prefetchImagesMobile = [];

  collageTabs &&
    collageTabs.length > 0 &&
    collageTabs.forEach(tab => {
      const [firstCollage, ...restCollages] = get(tab, 'collages', []);
      prefetchImagesDesktop.push(
        get(firstCollage, 'fields.image.value[2x1]', '') ||
        get(firstCollage, 'fields.gallery[0].fields.image.value[2x1]', '')
      );
      preProcessCollageImages(restCollages).forEach(image => {
        prefetchImagesDesktop.push(
          get(image, 'fields.image.value[2x1]', '') ||
          get(image, 'fields.gallery[0].fields.image.value[2x1]', '')
        );
      });
    });

  collageTabs &&
    collageTabs.length > 0 &&
    collageTabs.forEach(tab =>
      get(tab, 'collages', '').forEach(image => {
        prefetchImagesMobile.push(
          get(image, 'fields.mobileImage.value[2x1]', '') ||
          get(image, 'fields.gallery[0].fields.mobileImage.value[2x1]', '')
        );
      })
    );

  return (
    <div className={classes.wrapper} data-locator={TESTCOMPONENT_CONTAINER}>
      <PrefetchImages
        mobileImageArray={prefetchImagesMobile}
        desktopImageArray={prefetchImagesDesktop}
      />
      <Grid>
        {segmentText && (
          <TextRotateAnimation
            condition={true}
            animateSettings={{
              willAnimate: true,
              selfTrigger: true,
              duration: '250ms',
            }}
          >
            <Title text={segmentText} />
          </TextRotateAnimation>
        )}
        {title && (
          <Typography
            variant="h2"
            component="h2"
            gutterBottom={false}
            className={classes.heading}
            wordBreak
            animateSettings={{
              willAnimate: true,
              selfTrigger: true,
            }}
          >
            <Text field={title} />
          </Typography>
        )}
        <Tabs
          tabs={createTabs}
          className="tabs-container"
          onTabChange={index => onTabChange(index)}
        />
      </Grid>
    </div>
  );
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(CollageComponent))
);
